import { Box, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import React from "react";
import AboutCard from "./AboutCard";
import BookCard from "./BookCard";

const useStyles = makeStyles((theme) => ({
    header: {
        color: "white",
        textAlign: "center",
        padding: "4%",
    },
    subHeader: {
        color: "white",
        textAlign: "center",
        minWidth: "60%",
    },
    gridContainer: {
        minWidth: "80%",
    },
    section: {
        paddingTop: "1%",
        paddingBottom: "3%",
        display: "flex",
        flexDirection: "row",
        flexWrap: "wrap",
        justifyContent: "space-around",
    },
    backButton: {
        float: "left",
    },
}));

export default function About() {
    const classes = useStyles();

    return (
        <div>
            <Typography variant="h2" className={classes.header}>
                About
            </Typography>

            <Box className={classes.gridContainer}>
                <Typography variant="h4" className={classes.subHeader}>
                    <span role="img" aria-label="star">
                        üëæ
                    </span>
                    Who Am I
                </Typography>
                <Box className={classes.section}>
                    <AboutCard
                        text={
                            "Hello World! My name is Peter and I am a sophomore studying computer science at Tufts University. I am originally from Rockland County, NY and I am thankful to have always lived within 30 minutes of a major city (NYC and now Boston). My main interests in technology include cyber security, web engineering, mobile development, machine learning, and blockchain. I have experience in all of these fields but look forward to diving deeper into all of them. Outside of tech, I like to hike, run, read, travel, camp, and play pickup basketball."
                        }
                    />
                </Box>

                <Typography variant="h4" className={classes.subHeader}>
                    <span role="img" aria-label="star">
                        üòé
                    </span>
                    Early Years
                </Typography>
                <Box className={classes.section}>
                    <AboutCard
                        text={
                            "I started programming when I was 13 and have been doing it consistently ever since. I started by learning Java from an older kid at my school who would teach me and two of my friends after school. After a couple years of learning the basics spent about a year making games in java such as unbeatable tic tac toe bot, a simple scientific calculator, and some text based maze games. Once I got to high school, I took classes in QBasic, C++, and Java. I also participated in my schools robotics team which went to the world championships all three years. I was thankful for the opportunity to learn programming from an early age, and wanted to share the so my friends and I set up a programming class at our local Community Center where we taught 7th through 10th graders the basics of C++. We also helped start a Lego robotics team at our local elementary school. In addition, I was a counselor at a Summer camp where I taught classes ranging from basic web design to Unity to programming in C#."
                        }
                    />
                </Box>

                <Typography variant="h4" className={classes.subHeader}>
                    <span role="img" aria-label="star">
                        üë®‚Äçüíª
                    </span>
                    College
                </Typography>
                <Box className={classes.section}>
                    <AboutCard
                        text={
                            "Going to college introduced me to a large community of people who share my interests. I have taken and have access to a wide variety of classes within the computer science field. Outside of classes, I am the head of development for the Tufts Blockchain Club as well as a member of JumboSEC, our cybersecurity club. Above all, my favorite part about programming is working with friends on meaningful or interesting projects. Currently I am working on a mobile app with a friend from our original group of after school coders in middle school. Although the time difference between Tufts in Boston and Caltech in California is 3 hours, where he is studying, we still manage to find time to work together and Zoom often. Last Summer we gained acceptance into Tufts‚Äô Entrepreneurship Accelerator and had a fantastic time learning about the non-technical aspects of starting a company with our product. Additionally, I have participated in several hackathons, which have been some of the most fun and educational times I have had so far in college. The collaborative and competitive spirit along with the great community of people now feels like home. I am still amazed that it all started by chance, when I accidentally stumbled upon my first hackathon a month into freshman year. I have come a long way since then and have won prizes and made great friends, so I hope to continue my streak of participating in hackathons at least twice a semester until I graduate."
                        }
                    />
                </Box>

                <Typography variant="h4" className={classes.subHeader}>
                    <span role="img" aria-label="star">
                        üìö
                    </span>
                    Favorite Books
                </Typography>
                <Box className={classes.section}>
                    <BookCard />
                </Box>
            </Box>
        </div>
    );
}
